document.addEventListener('DOMContentLoaded', function(){ // Primero que todo el documento HTML este listo, despues llamo a la funcion
    crearGaleria();
});

function crearGaleria(){
    const galeria = document.querySelector('.galeria-imagenes');


    // for para recorrer la carpeta de imagenes y colocarlas en el HTML o web 
    for(i = 1; i <= 12; i++){ // recorre desde la img 1 hasta la 12
        const imagen = document.createElement('IMG'); // crea la etiqueta IMG y la almacena en una constante 
        imagen.src = `build/img/thumb/${i}.webp`; // se le da la direccion de donde estas la imagnes en su posicion
        imagen.dataset.imagenId = i;

        //AÃ±adir la funcion de mostrar imagenes
        imagen.onclick = mostrarImagen;

        const lista = document.createElement('LI'); // crea una etiqueta LI y la almacena en un constante 
        lista.appendChild(imagen); // luego convierte la etiqueta LI en hijo de la etiqueta IMG y le pasa las imagenes

        galeria.appendChild(lista); // luego convierte la etiqueta LI en hijo de la galeria o el UL y le pasa las imagenes
    }
}

// Funcion que muestra imagenes de la galeria en grande al dar click

function mostrarImagen(e){
    const id = parseInt(e.target.dataset.imagenId);

    //generar imagen
    const imagen = document.createElement('IMG');
    imagen.src = `build/img/grande/${id}.webp`; 

    const overley = document.createElement('DIV');
    overley.appendChild(imagen);
    overley.classList.add('overlay');

    // cuando se da click afuera de la imagen, se cierra
    overley.onclick = function(){
        overley.remove();
        body.classList.remove('fijar-body'); // elimina el modo olculto del scroll
    }

    //boton para cerrar la imagen
    const cerrarImagen = document.createElement('P');
    cerrarImagen.textContent = 'X';
    cerrarImagen.classList.add('btn-cerrar');
    
    // Cerrar imagen al presionar el boton
    cerrarImagen.onclick = function(){
        overley.remove();
        body.classList.remove('fijar-body');
    }

    overley.appendChild(cerrarImagen);

    //mostrar en el HTML
    const body = document.querySelector('body');
    body.appendChild(overley);
    body.classList.add('fijar-body');

}
