document.addEventListener('DOMContentLoaded', function(){
    scrollNav();

    navegacionFija();
})

function navegacionFija(){

    const barra = document.querySelector('.header');

    // Registar el Intersection Observer
    const observer = new IntersectionObserver( function(entries){
        if(entries[0].isIntersecting){
            barra.classList.remove('fijo');
        }else{
            barra.classList.add('fijo');
        }
    });

    //Elemento a observar
    observer.observe(document.querySelector('.sobre-festival'));
}

function scrollNav(){
    const enlaces = document.querySelectorAll('.navegacion-principal');

    // primero iterar sobre cada enlace
    enlaces.forEach(function(enlaces) {
        enlaces.addEventListener('click', function(e){ // escuchar el evento clicl y pasarle ele envento
            e.preventDefault();// detener el evento
            const seccion = document.querySelector(e.target.attributes.href.value); // entra a acada uno para sacar el valor

            // agregar un escroll lento 
            seccion.scrollIntoView({
                behavior: 'smooth'
            })
        })
    });

}